.language {
  color: black;
  position: relative;
  width: 5%;
  height: 50px;
  cursor: pointer;
}

.language img {
  width: 50px;
}

.chosen-language {
  height: 50px;
  width: 50px;
  padding: 5px;
}
.chosen-language:hover {
  background: rgba(138, 162, 122, 0.5);
}
.languages {
  position: absolute;
  left: 0;
  top: 0;
}

.languages > * {
  /*border: 1px solid #000;*/
  cursor: pointer;
  height: 50px;
  padding: 5px;
}

.languages > *.selected {
  /*background: #8aa27a;*/
}

.languages > *:hover {
  background: rgba(138, 162, 122, 0.5);
}
