.cardInfoLine {
  display: flex;
  justify-content: space-between;
}

.cardInfoLineTitle {
  font-weight: bold;
}

.cardInfoColumn > .cardInfoLine > .cardInfoLineTitle {
  text-align: right;
  padding: 0 5px;
}

@media screen and (max-width: 550px) {
  .cardInfoColumn > .cardInfoLine > .cardInfoLineTitle {
    padding: 5px 5px;
  }
}
