.modal-overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);
}

.modal-container {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.modal-border-wrap {
  max-width: 350px;
  padding: 1rem;
  position: relative;
  background: linear-gradient(
    15deg,
    rgb(148, 114, 71) 0%,
    rgb(241, 200, 141) 50%,
    rgb(148, 114, 71) 100%
  );
  /*linear-gradient(to right, red, purple);*/
  padding: 3px;
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
}

.modal {
  padding: 30px 10px;
  background: white;
  color: #548372;
  font-style: italic;
}
