@import "./App/Admin/Guests.css";
@import "./App/Admin/LoginPage.css";
@import "./Card/Card.css";
@import "./Card/CardHeader.css";
@import "./Card/CardContent.css";
@import "./Card/CardInfoLine.css";
@import "./Card/CardInfoColumn.css";

.App {
  text-align: center;
  white-space: pre-line;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background: white;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.placeholder {
  width: 5%;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.forma-container {
  opacity: 0;
}
